.booksearch {
  width: 50vw;
  height: 15vh;
  border: solid 1px rgb(196, 196, 196);
  box-shadow: 3px 2px 2px black;
  margin-top: 10px;
  border-radius: 5px;
}

.searchinput {
  position: relative;
  line-height: 10px;
  top: 25px;
}

.searchbtn {
  position: relative;
  top: 15px;
}
.results-container {
  height: auto;
  width: 75vw;
  border: solid 1px rgb(196, 196, 196);
  box-shadow: 3px 2px 2px black;
  margin-top: 10px;
  border-radius: 5px;
}
.view {
  color: white;
  text-decoration: none;
}

.view :link {
  color: white;
  text-decoration: none;
}

.view :hover {
  color: white;
  text-decoration: none;
}

.view :visited {
  color: white;
  text-decoration: none;
}

.bookitem {
  border: solid black 1px;
  margin-top: 5px;
  border: solid 1px rgb(196, 196, 196);
  box-shadow: 3px 2px 2px black;
  border-radius: 5px;
  margin-bottom: 5px;
}

.bookinfo {
  margin-top: 10px;
}

.bookinfo p {
  height: auto;
  line-height: 15px;
  position: relative;
  top: 10px;
}

.bookbtns button {
  float: right;
  position: relative;
  top: 10px;
  left: 20px;
  margin-right: 10px;
}

.bookbottom {
  height: auto;
  overflow: auto;
  margin-bottom: 10px;
}

.bookimg {
  float: left;
  margin-right: 15px;
}

.bookbottom {
  text-indent: 15px;
  word-wrap: break-word;
}

header {
  height: 6vh;
}

.saved {
  position: relative;
  margin-left: 20px;
}
.title {
  height: 35vh;
  border: solid 1px rgb(196, 196, 196);
  box-shadow: 3px 2px 2px black;
  margin-top: 15px;
  background-image: url(/static/media/booktitle.14b223c0.jpg);
  background-size: 100% 100%;
  width: 50vw;
  border-radius: 5px;
}

.intro {
  position: relative;
  top: 77%;
  text-align: center;
  font-size: 25px;
  line-height: 15px;
  color: white;
  -webkit-text-stroke: 1px rgb(56, 56, 56);
  text-shadow: 1px 2px 1px rgb(0, 0, 0)
}
